import React from "react";
import { Link, graphql } from 'gatsby';
import Layout from "../components/layout";
import * as style from '../styles/style.module.css';

const BlogList = ({ pageContext, data }) => {
  const posts = data.allMdx.edges;
  const { currentPage, numPages } = pageContext;
  const isFirst = currentPage === 1;
  const isLast = currentPage === numPages;
  const prevPage = (currentPage - 1) === 1 ? "/" : (currentPage - 1).toString();
  const nextPage = (currentPage + 1).toString();
  const prevPath = prevPage === "/" ? "/memo" : "/memo/";
  const nextPath = "/memo/";
  return (
    <Layout
      pageType="blog"
      pageTitle={`メモリスト ページ ${currentPage}`}
      pageDescription={`月の森デザインの Web研究室「ツキノラボ」のメモリストです。${currentPage}ページ目です。`}
    >
      <ul className={style.blogList}>
        {posts.map(({ node }) => {
          const slug = node.slug;
          const { title } = node.frontmatter;
          return (
            <li key={slug} className={style.blogList__item}>
              <Link to={`/memo/${slug}`} className={style.blogList__link}>
                {title || slug}
              </Link>
            </li>
          );
        })}
      </ul>
      <div className={style.pagenation}>
        {!isFirst && (
          <Link
            to={`${prevPath}${prevPage}`}
            rel="prev"
            className={`${style.pagenationLink} ${style.pagenationLink__prev}`}>
              前へ
          </Link>
        )}
        {!isLast && (
          <Link
            to={`${nextPath}${nextPage}`}
            rel="next"
            className={`${style.pagenationLink} ${style.pagenationLink__next}`}>
              次へ
          </Link>
        )}
      </div>

    </Layout>
  )
}

export const blogListQuery = graphql`
  query blogListQuery($skip: Int!, $limit: Int!) {
    allMdx(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          slug
          frontmatter {
            title
          }
        }
      }
    }
  }
`;

export default BlogList;
